  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.offer-info__list {
  margin-bottom: 20px;
  width: 100%;
  overflow-x: hidden;
}
.offer-info__list-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}
.offer-info__list-item span {
  font-family: var(--secondary-font);
  font-size: 16px;
  font-weight: 400;
  letter-spacing: -0.16px;
  color: var(--secondary);
}
.offer-info__list-item span:first-child {
  display: inline-block;
  margin-right: 20px;
  min-width: 100px;
  background-color: var(--white);
}
.offer-info__list-item span:last-child {
  position: relative;
  text-align: end;
}
.offer-info__text {
  font-family: var(--secondary-font);
  font-size: 16px;
  font-weight: 400;
  letter-spacing: -0.16px;
  text-align: center;
  color: #a9a9a9;
}
.offer-info__text a {
  color: var(--primary);
}

.product-detail__grid {
  display: grid;
  grid-template-columns: 550px auto 480px;
  gap: 20px;
}
@media (max-width: 1480px) {
  .product-detail__grid {
    grid-template-columns: 550px auto;
  }
}
@media (max-width: 960px) {
  .product-detail__grid {
    display: flex;
    flex-direction: column;
  }
}
@media (max-width: 1480px) {
  .product-detail__grid-section:nth-child(3) {
    grid-column: 1/3;
  }
}
@media (max-width: 960px) {
  .product-detail__grid-section:nth-child(2) {
    grid-column: 1/3;
  }
}

